.app-Wrapper {
	min-height: 100vh;
	display: flex;
	align-items: center;
	@media screen and (max-width: 767px) {
		flex-direction: column;
	}
}
.app-Logo {
	position: fixed;
	top: 16px;
	left: 16px;
	z-index: 9;
}
.app-Wrapper_Stream {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
}
.app-Wrapper_StreamLocal {
	max-width: 740px;
	@media screen and (max-width: 767px) {
		width: 100%;
		margin-bottom: 32px;
		height: 100vh;
	}
}
.app-Wrapper_StreamLocal-fixed {
	position: fixed;
	top: 16px;
	right: 16px;
	max-width: 250px;
	z-index: 1;
	height: auto;
	@media screen and (max-width: 767px) {
		max-width: 150px;
	}
}
.app-Wrapper_StreamLocalWrapper {
	position: relative;
	border-radius: var(--border-radius);
	overflow: hidden;
	display: flex;
	height: 100%;
}
.app-Wrapper_StreamLocalWrapperActions {
	position: absolute;
	bottom: 8px;
	width: 100%;
	text-align: center;
	button {
		margin: 0 8px;
	}
}
.app-Wrapper_StreamRemote {
	position: x;
	width: 100%;
	background-color: #f5f5f5;
	text-align: center;
	.sw-Camera {
		height: 100vh;
	}
}
.app-Wrapper_StreamRemoteWaiting {
	display: flex;
	height: 100vh;
	width: 100%;
	align-items: center;
	justify-content: center;
}
.app-MakeCall {
	text-align: center;
	padding: 16px;
}
.app-Wrapper_Actions {
	min-width: 300px;
	text-align: center;
	img {
		margin-bottom: 16px;
	}
}
